import BaseFormField from "@/components/shared/forms/BaseFormField";

export default {
  components: {
    BaseFormField,
  },
  props: ["title", "error", "placeholder", "value"],
  computed: {
    dataValue: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      },
    },
  },
};
