<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main' }" class="breadcrumbs__link"> Каталог </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'cart' }" class="breadcrumbs__link"> Корзина </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">
        <div v-if="orderInfo">
          Заказ оформлен <span>№ {{ orderInfo.id }}</span>
        </div>
        <div v-else>Нет данных</div>
      </h1>
    </div>

    <section class="cart">
      <div v-if="orderInfo" class="cart__form form">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо
            с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для
            уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель </span>
              <span class="dictionary__value"> {{ orderInfo.name }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес доставки </span>
              <span class="dictionary__value"> {{ orderInfo.address }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон </span>
              <span class="dictionary__value"> {{ orderInfo.phone }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{ orderInfo.email }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value"> картой при получении </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li v-for="item in basketItems" :key="item.id" class="cart__order">
              <h3>
                {{ item?.product?.title }} <sup v-if="item.quantity > 1">x{{ item.quantity }}</sup>
              </h3>
              <b>{{ item.price | numberFormat }} ₽</b>
              <span>Артикул: {{ item.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p class="hidden">Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b>{{ basketItems.length }}</b> товара на сумму
              <b>{{ orderInfo.totalPrice | numberFormat }} ₽</b>
            </p>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { mapState } from "vuex";
import numberFormat from "@/helpers/numberFormat";

export default {
  created() {
    if (this.orderInfo && this.orderInfo.id === this.$route.params.id) return;

    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
  },
  computed: mapState({
    orderInfo: (state) => state.orderInfo,
    basketItems: (state) => state.orderInfo.basket.items,
  }),
  filters: {
    numberFormat,
  },
};
</script>
