<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> {{ productsCount }} товара </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :filter.sync="filter" :page.sync="page" />

      <section class="catalog">
        <AppLoader v-if="productsLoading" />
        <div v-if="productsLoadingFailed">
          <p class="text-red">Произошла ошибка при загрузке товаров</p>
          <button @click.prevent="loadProducts">Попробовать ещё раз</button>
        </div>

        <ProductList :products="products" />
        <BasePagination :count="productsCount" v-model="page" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import axios from "axios";
import ProductList from "@/components/product/ProductList";
import BasePagination from "@/components/shared/BasePagination";
import ProductFilter from "@/components/product/ProductFilter";
import AppLoader from "@/components/shared/layout/AppLoader";
import { API_BASE_URL } from "@/config";

export default {
  components: {
    ProductList,
    BasePagination,
    ProductFilter,
    AppLoader,
  },
  data() {
    return {
      filter: {
        priceFrom: 0,
        priceTo: 0,
        catId: 0,
        colorId: 0,
      },

      productsData: null,
      page: 1,
      productsPerPage: 9,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      if (!this.productsData) return [];

      return this.productsData.items.map((product) => {
        return {
          ...product,
          image: product.image.file.url,
        };
      });
    },
    productsCount() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filter() {
      this.loadProducts();
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;

      return axios
        .get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filter.catId,
            minPrice: this.filter.priceFrom,
            maxPrice: this.filter.priceTo,
            colorId: this.filter.colorId,
          },
        })
        .then((response) => {
          this.productsData = response.data;
        })
        .catch(() => (this.productsLoadingFailed = true))
        .then(() => (this.productsLoading = false));
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
