import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/config";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    cartProductLoading: false,
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  },
  mutations: {
    updateUserAccessKey(state, newKey) {
      state.userAccessKey = newKey;
    },
    updateCartProductsData(state, newData) {
      state.cartProductsData = newData;
    },
    updateCartProductsLoading(state, newState) {
      state.cartProductLoading = newState;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => {
        return {
          productId: item.product.id,
          count: item.quantity,
        };
      });
    },
    updateCartProductCount(state, { productId, count }) {
      const item = state.cartProducts.find((p) => p.productId === productId);

      if (item) item.count = count;
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((p) => p.productId !== productId);
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateOrderInfo(state, info) {
      state.orderInfo = info;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const product = state.cartProductsData.find((p) => p.product.id === item.productId).product;

        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => item.product.price * item.count + acc,
        0
      );
    },
  },
  actions: {
    loadCart(context) {
      context.commit("updateCartProductsLoading", true);

      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem("userAccessKey", response.data.user.accessKey);
            context.commit("updateUserAccessKey", response.data.user.accessKey);
          }

          context.commit("updateCartProductsLoading", false);
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        })
        .catch(() => context.commit("updateCartProductsLoading", false));
    },
    addProductToCart(context, { productId, count }) {
      return axios
        .post(
          `${API_BASE_URL}/api/baskets/products`,
          {
            productId,
            quantity: count,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        });
    },
    updateCartProductCount(context, { productId, count }) {
      if (!count) return;

      context.commit("updateCartProductCount", { productId, count });

      return axios
        .put(
          `${API_BASE_URL}/api/baskets/products`,
          {
            productId,
            quantity: count,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          }
        )
        .then((response) => context.commit("updateCartProductsData", response.data.items))
        .catch(() => context.commit("syncCartProducts"));
    },
    deleteCartProduct(context, productId) {
      if (!productId) return;

      context.commit("deleteCartProduct", productId);

      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            productId: productId,
          },
        })
        .then((response) => context.commit("updateCartProductsData", response.data.items))
        .catch(() => context.commit("syncCartProducts"));
    },
    loadOrderInfo(context, orderId) {
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit("updateOrderInfo", response.data);
        });
    },
  },
});
