<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title" />
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <!-- <p class="product__info product__info--color">
      Цвет:
      <span>
        <i style="background-color: #ff6b00"></i>
        Оранжевый
      </span>
    </p> -->
    <span class="product__code"> Артикул: {{ item.productId }} </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click="decrement">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="count" name="count" />

      <button type="button" aria-label="Добавить один товар" @click="increment">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price"> {{ (item.count * item.product.price) | numberFormat }} ₽ </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click="deleteProduct(item.productId)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import { mapActions } from "vuex";
import numberFormat from "@/helpers/numberFormat";

export default {
  props: ["item"],
  methods: {
    ...mapActions(["deleteCartProduct"]),
    increment() {
      this.count += 1;
    },
    decrement() {
      if (this.count > 1) this.count -= 1;
    },
    deleteProduct(productId) {
      this.deleteCartProduct(productId);
    },
  },
  computed: {
    count: {
      get() {
        return this.item.count;
      },
      set(val) {
        this.$store.dispatch("updateCartProductCount", {
          productId: this.item.productId,
          count: val,
        });
      },
    },
  },
  filters: {
    numberFormat,
  },
};
</script>
