<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main' }" class="breadcrumbs__link"> Каталог </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'cart' }" class="breadcrumbs__link"> Корзина </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Оформление</h1>
      <span class="content__info"> {{ products.length }} товара </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              :error="formError.name"
              title="ФИО"
              placeholder="Введите ваше полное имя"
            />

            <BaseFormText
              v-model="formData.address"
              :error="formError.address"
              title="Адрес доставки"
              placeholder="Введите ваш адрес"
            />

            <BaseFormText
              v-model="formData.phone"
              :error="formError.phone"
              type="tel"
              title="Телефон"
              placeholder="Введите ваш телефон"
            />

            <BaseFormText
              v-model="formData.email"
              :error="formError.email"
              type="email"
              title="Email"
              placeholder="Введите ваш Email"
            />

            <BaseFormTextarea
              v-model="formData.comments"
              :error="formError.comments"
              title="Комментарий к заказу"
              placeholder="Ваши пожелания"
            />
          </div>

          <div class="cart__options hidden">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value"> Самовывоз <b>бесплатно</b> </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500" />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card" />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash" />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li v-for="item in products" :key="item.productId" class="cart__order">
              <h3>{{ item.product.title }}</h3>
              <b>{{ (item.count * item.product.price) | numberFormat }} ₽</b>
              <span>Артикул: {{ item.productId }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p class="hidden">Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b>{{ products.length }}</b> товара на сумму
              <b>{{ totalPrice | numberFormat }} ₽</b>
            </p>
          </div>

          <button
            class="cart__button button button--primery"
            type="submit"
            :disabled="!products.length || orderSending"
          >
            Оформить заказ
          </button>
        </div>

        <AppLoader v-if="$store.state.cartProductLoading || orderSending" />

        <div v-if="orderSendErrorMessage" class="cart__error form__error-block">
          <h4>Заявка не отправлена!</h4>
          <p>{{ orderSendErrorMessage }}</p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from "vuex";
import axios from "axios";
import AppLoader from "@/components/shared/layout/AppLoader";
import BaseFormText from "@/components/shared/forms/BaseFormText";
import BaseFormTextarea from "@/components/shared/forms/BaseFormTextarea";
import numberFormat from "@/helpers/numberFormat";
import { API_BASE_URL } from "@/config";

export default {
  components: {
    AppLoader,
    BaseFormText,
    BaseFormTextarea,
  },
  data() {
    return {
      formData: {},
      formError: {},
      orderSending: false,
      orderSendErrorMessage: "",
    };
  },
  methods: {
    order() {
      this.orderSending = true;
      this.formError = {};
      this.orderSendErrorMessage = "";

      return axios
        .post(
          `${API_BASE_URL}/api/orders`,
          {
            ...this.formData,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          console.log(response);
          this.$store.commit("resetCart");
          this.$store.commit("updateOrderInfo", response.data);
          this.$router.push({ name: "orderInfo", params: { id: response.data.id } });
        })
        .catch((error) => {
          console.log(error);
          this.formError = error.response.data.error.request || {};
          this.orderSendErrorMessage = error.response.data.error.message;
        })
        .then(() => (this.orderSending = false));
    },
  },
  computed: {
    ...mapGetters({ products: "cartDetailProducts", totalPrice: "cartTotalPrice" }),
  },
  filters: {
    numberFormat,
  },
};
</script>
