<script>
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default {
  props: ["filter"],
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCatId: 0,
      currentColorId: 0,

      categoriesData: null,
      colorsData: null,
    };
  },
  computed: {
    cats() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      if (!this.colorsData) return [];

      const fc = this.colorsData.items.reduce((o, i) => {
        if (!o.find((v) => v.code == i.code)) o.push(i);
        return o;
      }, []);

      return fc;
    },
  },
  watch: {
    filter(val) {
      this.currentPriceFrom = val.priceFrom;
      this.currentPriceTo = val.priceTo;
      this.currentCatId = val.catId;
      this.currentColorId = val.colorId;
    },
  },
  methods: {
    submit() {
      const changedFilter = {
        priceFrom: this.currentPriceFrom,
        priceTo: this.currentPriceTo,
        catId: this.currentCatId,
        colorId: this.currentColorId,
      };

      this.$emit("update:filter", changedFilter);
      this.$emit("update:page", 1);
    },
    reset() {
      const changedFilter = {
        priceFrom: 0,
        priceTo: 0,
        catId: 0,
        colorId: 0,
      };

      this.$emit("update:filter", changedFilter);
    },
    loadCategories() {
      return axios.get(`${API_BASE_URL}/api/productCategories`).then((response) => {
        this.categoriesData = response.data;
      });
    },
    loadColors() {
      return axios.get(`${API_BASE_URL}/api/colors`).then((response) => {
        this.colorsData = response.data;
      });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};
</script>
